html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
}
body.fijarBody {
  overflow: hidden;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

p {
  font-size: 1.8rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #4cb8b3;
}

.titulo {
  margin: 0 auto;
  color: #ffffff;
  text-align: center;
}

.navegacionPrincipal a {
  display: block;
  color: #ffffff;
  font-size: 2rem;
  padding-bottom: 2rem;
  text-align: center;
}
.navegacionPrincipal a:hover {
  color: #000000;
}
@media (min-width: 768px) {
  .navegacionPrincipal a {
    padding-bottom: 0;
    padding-right: 2rem;
  }
}

@media (min-width: 768px) {
  .contenidoHeader {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacionPrincipal {
    display: flex;
    align-items: center;
  }
}

.video {
  position: relative;
  max-height: 70rem;
  max-height: 70rem;
  overflow: hidden;
}

.over {
  position: absolute;
  color: #ffffff;
  background: rgb(132, 0, 255);
  background: linear-gradient(270deg, rgba(132, 0, 255, 0.5) 31%, rgba(0, 121, 255, 0.3) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 1;
}

video {
  display: block;
}

.video_contenido h2 {
  margin: 0;
  font-size: 6rem;
}

.video_contenido p {
  width: fit-content;
  margin: 0;
  background-color: #F53756;
  padding: 1rem;
  font-weight: bold;
}

@media (min-width: 992px) {
  .section {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    padding: 5rem;
    margin: 0 auto;
  }
}
.section_titulo {
  color: #F53756;
  font-weight: bold;
}

.section_imagen {
  padding-top: 2rem;
  min-width: 40rem;
}

.principal {
  background-color: #F53756;
}

.principal_texto {
  text-align: center;
  color: #ffffff;
  padding-top: 1rem;
}

.principal_tarjetaAmarilla {
  background-color: #fef46d;
  display: grid;
  text-align: center;
  padding-bottom: 2rem;
}

.principal_tarjetaAmarilla_encabezado img {
  max-width: 15rem;
  margin: 0 auto;
  padding-bottom: 1rem;
}

.principal_tarjetaAmarilla_cuerpo span {
  font-weight: bold;
}

.principal_tarjetaAmarilla_cuerpo li:nth-of-type(odd) {
  background-color: #d1c201;
}

.principal_tarjetaVerde {
  background-color: #4cb8b3;
  display: grid;
  text-align: center;
  padding-bottom: 2rem;
}

.principal_tarjetaVerde_encabezado img {
  max-width: 15rem;
  margin: 0 auto;
  padding-bottom: 1rem;
}

.principal_tarjetaVerde_cuerpo span {
  font-weight: bold;
}

.principal_tarjetaVerde_cuerpo li:nth-of-type(odd) {
  background-color: #3b9691;
}

@media (min-width: 992px) {
  .tarjetero {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin: 0 5rem;
    padding-bottom: 5rem;
  }
  .principal_tarjetaVerde_cuerpo li {
    font-size: 2rem;
    padding: 1rem 0;
  }
  .principal_tarjetaAmarilla_cuerpo li {
    font-size: 2rem;
    padding: 1rem 0;
  }
}
.galeria_imagenes img {
  margin: 0 auto;
  margin-bottom: 1rem;
}

.galeria h3 {
  text-align: center;
}

.galeria_imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 768px) {
  .galeria_imagenes {
    grid-template-columns: repeat(3, 3fr);
  }
}
@media (min-width: 992px) {
  .galeria_imagenes {
    grid-template-columns: repeat(6, 4fr);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #F53756;
  display: grid;
  place-items: center;
  font-weight: bold;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  background-color: #4cb8b3;
  width: 100%;
  height: 100%;
}

.boletos h3 {
  color: #ffffff;
  text-align: center;
  padding-top: 2rem;
  margin: 0;
}

.boletos h4 {
  color: #ffffff;
  text-align: center;
  font-weight: 400;
}

.boletos_titulo {
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #F53756;
  margin-bottom: 0;
  padding: 1rem;
}

.boleto_informacion {
  background-color: #ffffff;
  line-height: 3;
}

.boleto_informacion li {
  margin-left: 2rem;
}

.boleto_precio {
  background-color: #752F97;
  color: #ffffff;
  text-align: center;
  margin-top: 0;
  padding: 1rem;
}

@media (min-width: 992px) {
  .boletos_informacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.footer {
  background-color: #F53756;
  margin: 0 auto;
  text-align: center;
  color: #ffffff;
}